<template>
	<div id="classify">
		<AllTitle :title="title" @onClickLeft="onClickLeft" />

		<div class="girlbooks">
			<HomeTitel>
				<template v-slot:left>
					<h3>女生小说</h3>
				</template>
			</HomeTitel>
			<ul class="girlclassify">
				<li
					v-for="classify in girlclassify"
					:key="classify.bookCount"
					@click="toclassify('female', classify.name)"
				>
					{{ classify.name }}
				</li>
			</ul>
		</div>
		<div class="boybooks">
			<HomeTitel>
				<template v-slot:left>
					<h3>男生小说</h3>
				</template>
			</HomeTitel>
			<ul class="boyclassify">
				<li
					v-for="classify in boyclassify"
					:key="classify.bookCount"
					@click="toclassify('male', classify.name)"
				>
					{{ classify.name }}
				</li>
			</ul>
		</div>

		<HomeFooter />
	</div>
</template>

<script>
import AllTitle from '../components/AllTitle.vue'
import HomeFooter from '../components/Home/HomeFooter.vue'
import HomeTitel from '../components/Home/HomeTitel.vue'
export default {
	data() {
		return {
			title: '分类',
			girlclassify: null,
			boyclassify: null,
		}
	},
	components: {
		AllTitle,
		HomeFooter,
		HomeTitel,
	},
	methods: {
		onClickLeft: function() {
			// console.log('onClickLeft')
			this.$router.push({
				path: '/',
			})
		},
		toclassify: function(gender, name) {
            // console.log(gender, name)
            this.$router.push({
                path: '/classifybooks',
                query:{
                    gender,
                    name
                }
			})
		},
	},
	created() {
		this.axios.get('http://novel.kele8.cn/categories').then((res) => {
			this.girlclassify = res.data.female
			this.boyclassify = res.data.male
			// console.log(this.girlclassify, this.boyclassify)
		})
	},
}
</script>

<style lang="less">
#classify {
	.girlbooks {
		.hometitle {
			.van-nav-bar__content {
				.van-nav-bar__left {
					h3 {
						border-left: 5px solid #ff024b;
					}
				}
			}
		}
		.girlclassify {
			padding: 0 20px;
			display: flex;
			flex-wrap: wrap;

			li {
				flex: 0 0 calc(50% - 18px);
				margin: 8px;
				height: 40px;
				border: 1px solid #ccc;
				border-radius: 20px;
				text-align: center;
				line-height: 40px;
				font-size: 14px;
				&:nth-child(-n + 3) {
					border-color: #ff024b;
					color: #ff024b;
				}
			}
		}
	}
	.boybooks {
		.hometitle {
			.van-nav-bar__content {
				.van-nav-bar__left {
					h3 {
						border-left: 5px solid #1cb09a;
					}
				}
			}
		}
		.boyclassify {
			padding: 0 20px;
			display: flex;
			flex-wrap: wrap;

			li {
				flex: 0 0 calc(50% - 18px);
				margin: 8px;
				height: 40px;
				border: 1px solid #ccc;
				border-radius: 20px;
				text-align: center;
				line-height: 40px;
				font-size: 14px;
				&:nth-child(-n + 3) {
					border-color: #1cb09a;
					color: #1cb09a;
				}
			}
		}
	}
}
</style>
