<template>
	<div id="searchbook">
		<AllTitle :title="title" @onClickLeft="onClickLeft" />
		<div class="search">
			<form action="/">
				<van-search
					v-model.trim="value"
					placeholder="请输入搜索关键词"
					@search="onSearch"
					@input="onChange"
				/>
			</form>
		</div>
		<div v-if="value" class="searchcomplete">
			<ul>
				<li
					v-for="(completeword, index) in completewords"
					:key="index"
					@click="completehandle(completeword)"
				>
					{{ completeword }}
				</li>
			</ul>
		</div>

		<div class="hotbooks" v-if="value === ''">
			<HomeTitel>
				<template v-slot:left>
					<h3>热门推荐</h3>
				</template>
			</HomeTitel>
			<ul>
				<li
					v-for="hotbook in hotbooks"
					:key="hotbook.book"
					@click="hotbookhandle(hotbook)"
				>
					{{ hotbook.word }}
				</li>
			</ul>
		</div>
		<HomeTitel @clickhandle="clickhandle" v-if="value === ''">
			<template v-slot:left>
				<h3>搜索历史</h3>
			</template>
			<template v-slot:right>
				<span>删除历史</span>
			</template>
		</HomeTitel>
		<ul class="historysearch" v-if="value === ''">
			<li
				v-for="historyitem in historysearch"
				:key="historyitem.time"
				@click="historyhandle(historyitem.data)"
			>
				{{ historyitem.data }}
			</li>
		</ul>
	</div>
</template>

<script>
import AllTitle from '../components/AllTitle.vue'
import HomeTitel from '../components/Home/HomeTitel.vue'
export default {
	components: { AllTitle, HomeTitel },

	data() {
		return {
			title: '搜索',
			value: '',
			hotbooks: null,
			completewords: null,
			historysearch: null,
		}
	},
	created() {
		this.axios.get('http://novel.kele8.cn/hot-books').then((res) => {
			// console.log(res)
			this.hotbooks = res.data.newHotWords
		})
		this.historysearch = localStorage.getarray('historysearch')
	},
	methods: {
		onClickLeft() {
			this.$router.push({
				path: '/',
			})
		},
		onSearch() {
			// console.log(this.value)
			let times = Date.now()
			this.historysearch = localStorage.getarray('historysearch') || []
			this.historysearch.unshift({
				data: this.value,
				time: times,
				expire: 1 * 60 * 60 * 1000,
			})

			localStorage.setItem(
				'historysearch',
				JSON.stringify(this.historysearch)
			)
			this.$router.push({
				path: '/searchresult',
				query: {
					value: this.value,
				},
			})
			this.value = ''
		},
		onChange() {
			// console.log(this.value);
			this.axios
				.get('http://novel.kele8.cn/auto-complete?query=' + this.value)
				.then((res) => {
					this.completewords = res.data.keywords
					// console.log(this.completewords)
				})
		},
		clickhandle() {
			// console.log('删除')
			localStorage.removeItem('historysearch')
			this.historysearch = localStorage.getarray('historysearch')
		},
		hotbookhandle(hotbook) {
			this.value = hotbook.word
			this.onSearch()
		},
		historyhandle(data) {
			console.log(data)
			this.$router.push({
				path: '/searchresult',
				query: {
					value: data,
				},
			})
		},
		completehandle(word) {
			this.value = word
			this.onSearch()
		},
	},
}
</script>

<style lang="less">
#searchbook {
	.hotbooks {
		ul {
			margin-top: 10px;
			padding: 0 10px;
			li {
				display: inline-block;
				padding: 5px 10px;
				margin-right: 10px;
				margin-bottom: 10px;
				background: #efeff4;
				font-size: 14px;
				color: #9bb2cd;
				border-radius: 20px;
			}
		}
	}
	.searchcomplete {
		ul {
			padding: 0 10px;
			li {
				padding: 5px;
				border-bottom: 1px solid #eee;
			}
		}
	}
	.historysearch {
		padding: 0 10px;
		li {
			padding: 10px;
			border-bottom: 1px solid #eee;
			font-size: 14px;
			color: #aaa;
		}
	}
}
</style>
