import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Ranking from '../views/Ranking.vue'
import RankFemale from '../views/RankFemale.vue'
import RankMale from '../views/RankMale.vue'
import Classify from '../views/Classify.vue'
import ClassifyBooks from '../views/ClassifyBooks.vue'
import FinishBooks from '../views/FinishBooks.vue'
import RankBooks from '../views/RankBooks.vue'
import BookDetails from '../views/BookDetails.vue'
import SearchBook from '../views/SearchBook.vue'
import ReadBook from '../views/ReadBook.vue'
import BookLists from '../views/BookLists.vue'
import SearchResult from '../views/SearchResult.vue'
import BookStack from '../views/BookStack.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/ranking',
    name: 'Ranking',
    component: Ranking,
    children:[
        {
            path: '/',
            component: RankFemale
        },
        {
            path: 'rankmale',
            component: RankMale
        }
    ]
  },
  {
    path: '/classify',
    name: 'Classify',
    component: Classify
  },
  {
    path: '/classifybooks',
    name: 'ClassifyBooks',
    component: ClassifyBooks
  },
  {
    path: '/finishbooks',
    name: 'FinishBooks',
    component: FinishBooks
  },
  {
    path: '/rankbooks',
    name: 'RankBooks',
    component: RankBooks
  },
  {
    path: '/bookdetails',
    name: 'BookDetails',
    component: BookDetails
  },
  {
    path: '/searchbook',
    name: 'SearchBook',
    component: SearchBook
  },
  {
    path: '/booklists',
    name: 'BookLists',
    component: BookLists
  },
  {
    path: '/readbook',
    name: 'ReadBook',
    component: ReadBook
  },
  {
    path: '/searchresult',
    name: 'SearchResult',
    component: SearchResult
  },
  {
    path: '/bookstack',
    name: 'BookStack',
    component: BookStack
  },
]

const router = new VueRouter({
  routes
})

export default router
