<template>
	<div id="classifybooks">
		<AllTitle
			:title="`${$route.query.name}小说`"
			@onClickLeft="onClickLeft"
		/>

		<ul v-if="classifybooks&&classifybooks.length>0">
			<li v-for="books in classifybooks" :key="books._id" @click="Tobook(books._id)">
				<DisplayBooks :books="books" />
			</li>
		</ul>
        <div v-else class="without">
            <h2>暂无小说</h2>
        </div>
	</div>
</template>

<script>
import AllTitle from '../components/AllTitle.vue'
import DisplayBooks from '../components/Home/DisplayBooks.vue'
export default {
    data() {
        return {
            classifybooks:null
        }
    },
	components: { AllTitle, DisplayBooks },
	methods: {
		
		onClickLeft: function() {
			// console.log(123)
			this.$router.push({
				path: '/classify',
			})
        },
        Tobook(id){
            // console.log(id);
            this.$router.push({
                path:"/bookdetails",
                query:{
                    gender:this.$route.query.gender,
                    name:this.$route.query.name,
                    id
                }
            })
        }
	},
	created() {
		// console.log(this.$route.query)
		this.axios
			.get(
				'http://novel.kele8.cn/category-info?gender=' +
					this.$route.query.gender +
					'&major=' +
					this.$route.query.name
			)
			.then((res) => {
                this.classifybooks = res.data.books
                
				// console.log(this.classifybooks)

			})
	},
}
</script>

<style lang="less">
#classifybooks{
    .without{
        text-align: center;
    }
}

</style>
