import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		chapter: null,
		prevpath: '/',
	},
	mutations: {
		updatedChapter: function(state, payload) {
			state.chapter = payload
		},
		updatedPrevpath: function(state, payload) {
			state.prevpath = payload
		},
	},
	actions: {},
	modules: {},
})
