<style>
.loading1 {
	width: 50px;
	height: 40px;
	margin: 60px auto;
	text-align: center;
}
.loading1 span {
	width: 5px;
	height: 100%;
    margin: 0 2px;
	display: inline-block;
	background: #b8232f;
	animation: mymove 1.2s infinite ease-in-out;
	-webkit-animation: mymove 1.2s infinite ease-in-out;
	/*  
                mymove代表动画名字
                1.2s代表执行时间
                infinite表示一直循环执行
                ease-in-out表示先慢后快的缓动
            */
}
.loading1 > span:nth-child(2) {
	-webkit-animation-delay: -1s;
	animation-delay: -1s;
}
.loading1 > span:nth-child(3) {
	-webkit-animation-delay: -0.9s;
	animation-delay: -0.9s;
}
.loading1 > span:nth-child(4) {
	-webkit-animation-delay: -0.8s;
	animation-delay: -0.8s;
}
.loading1 > span:nth-child(5) {
	-webkit-animation-delay: -0.7s;
	animation-delay: -0.7s;
}
@keyframes mymove {
	0% {
		transform: scaleY(0.4);
	}
	25% {
		transform: scaleY(1);
	}
	50% {
		transform: scaleY(0.4);
	}
	75% {
		transform: scaleY(0.4);
	}
	100% {
		transform: scaleY(0.4);
	}
}
@-webkit-keyframes mymove {
	0% {
		transform: scaleY(0.4);
	}
	25% {
		transform: scaleY(1);
	}
	50% {
		transform: scaleY(0.4);
	}
	75% {
		transform: scaleY(0.4);
	}
	100% {
		transform: scaleY(0.4);
	}
}
</style>
