<template>
	<div id="searchresult">
		<AllTitle :title="`${$route.query.value}`+'的相关书籍'" @onClickLeft="onClickLeft" />
		<ul v-if="searchbooks" class="searchbooks">
			<li
				v-for="book in searchbooks"
				:key="book._id"
				@click="Tobook(book._id)"
			>
				<van-card
					:title="book.title"
					:thumb="`http://statics.zhuishushenqi.com${book.cover}`"
				>
					<template #desc>
						<h4>{{ book.author }} | {{ book.cat }}</h4>
						<h4>{{ book.shortIntro }}</h4>
						<h3>
							<span>{{ book.latelyFollower }}</span
							>人气 | <span>{{ book.retentionRatio }}</span
							>%读者存留
						</h3>
					</template>
				</van-card>
			</li>
		</ul>
        <div v-else>
            <Loading />
        </div>
	</div>
</template>

<script>
import AllTitle from '../components/AllTitle.vue'
import Loading from '../components/Loading.vue'
export default {
	data() {
		return {
			searchbooks: null,
		}
	},
	components: { AllTitle, Loading },
	created() {
		this.axios
			.get(
				'http://novel.kele8.cn/search?keyword=' +
					this.$route.query.value
			)
			.then((res) => {
				this.searchbooks = res.data.books
				// console.log(this.searchbooks)
			})
	},
	methods: {
		onClickLeft() {
            this.$router.push({
                path:'/searchbook'
            })
        },
		Tobook(id) {
            console.log(id);
            this.$router.push({
                path:"/bookdetails",
                query:{
                    id
                }
            })
        },
	},
}
</script>

<style lang="less">
#searchresult {
	.searchbooks {
		li {
            border-bottom: 1px solid #eee;
			.van-card {
				.van-card__header {
					.van-card__thumb {
						width: 60px;
					}
					.van-card__content {
						.van-card__title {
							font-weight: 700;
							font-size: 14px;
							line-height: 20px;
						}
						h4 {
							white-space: nowrap;
							text-overflow: ellipsis;
							overflow: hidden;
							margin: 0;
							font-weight: normal;
							color: #aaa;
							padding: 3px 0;
						}
						h3 {
							margin: 0;
							color: #999;
							font-size: 12px;
							padding: 3px 0;
							font-weight: normal;
							span {
								color: #b93221;
							}
						}
					}
				}
			}
		}
    }
    .alltitle{
        .van-nav-bar__content{
            .van-nav-bar__title{
                font-size: 14px;
            }
        }
    }
}
</style>
