<template>
	<ul id="reviews">
		<li v-for="review in reviews" :key="review._id">
			<van-card
				desc="描述信息"
				:title="review.author.nickname"
				:thumb="
					`http://statics.zhuishushenqi.com${review.author.avatar}`
				"
			>
				<template #desc>
					<h2>{{ review.title }}</h2>
					<h3>{{ review.content }}</h3>
				</template>
				<template #footer>
					<span>{{ review.likeCount }}人觉得有用</span>
				</template>
			</van-card>
		</li>
	</ul>
</template>

<script>
export default {
	props: ['reviews'],
}
</script>

<style lang="less">
#reviews {
