<script>


Storage.prototype.getarray = (array) => {
	let val = localStorage.getItem(array)
	let obj = {}
	if (!val) {
		return val
	}
	obj = JSON.parse(val)
	// this.todos = this.todos.filter((t) => !t.completed)
	obj = obj.filter((e) => Date.now() - e.time <= e.expire)
	localStorage.setItem(array, JSON.stringify(obj))

	// console.log(obj)

	return obj
}
