<template>
	<div class="readbook" :style="{ background: changebg }">
		<transition name="headermove">
			<div class="headnav" v-show="showtool">
				<van-nav-bar
					left-text="返回"
					left-arrow
					@click-left="
						$router.push({
							path: '/bookdetails',
							query: { id: $route.query.id },
						})
					"
					@click-right="homeshow = !homeshow"
				>
					<template #right>
						<van-icon name="wap-nav" />
					</template>
				</van-nav-bar>
				<ul v-show="homeshow">
					<li @click="$router.push({ path: '/' })">
						<van-icon name="wap-home" />首页
					</li>
					<li @click="$router.push({ path: '/bookstack' })">
						<van-icon name="column" />书架
					</li>
					<li><van-icon name="todo-list" />详情</li>
				</ul>
			</div>
		</transition>

		<div v-if="book" class="wrapper" ref="wrapper">
			<ul class="shade">
				<li class="prevpage" @click="prevpage">
					<div class="shadetip" v-if="isshadetip">
						上一页
					</div>
				</li>
				<li class="sectorization" @click="clickhandle">
					<div class="shadetip" v-if="isshadetip">
						菜单
					</div>
				</li>
				<li class="nextpage" @click="nextpage">
					<div class="shadetip" v-if="isshadetip">
						下一页
					</div>
				</li>
			</ul>
			<div
				class="bookcontent"
				ref="bookcontent"
				:style="{ background: changebg }"
			>
				<h2 class="title" :style="{ color: changefc }" ref="title">
					{{ book.title }}
				</h2>
				<p
					v-for="(cpcontent, index) in cpcontents"
					:key="index"
					class="content"
					:style="{ fontSize: fontSize + 'px', color: changefc }"
				>
					{{ cpcontent }}
				</p>
			</div>
			<transition name="leftmove">
				<div class="chapteritem" v-show="showchapter"  ref="chapteritem">
					<h3 >目录</h3>
					<ul >
						<li
							v-for="chapteritem in chapter.chapters"
							:key="chapteritem.id"
							:class="{
								itemactive:
									chapteritem.order - 1 ===
									lacchapter[0].section,
							}"
							@click="
								readbook(chapteritem.order, chapteritem.isVip)
							"
						>
							{{ chapteritem.order }} {{ chapteritem.title }}
							<i v-if="chapteritem.isVip"></i>
						</li>
					</ul>
				</div>
			</transition>
			<transition name="footermove">
				<div class="footernav" v-if="showtool">
					<div class="change-font-size">
						<span
							class="changebtn"
							@click="abatehandle"
							:class="{ changeactive: progress === 0 }"
							>Aa-</span
						>
						<div class="progressbar">
							<van-progress
								pivot-text=""
								color="#f2826a"
								:percentage="progress"
							/>
						</div>

						<span
							class="changebtn"
							@click="addhandle"
							:class="{ changeactive: progress === 100 }"
							>Aa+</span
						>
					</div>
					<div class="change-background">
						<van-button
							hairline
							@click="defaulthandle"
							:class="{ changbgactive: changebg === '#eee6dd' }"
							>默认</van-button
						>
						<van-button
							hairline
							@click="nightshandle"
							:class="{ changbgactive: changebg === '#0c0c0c' }"
							>夜间</van-button
						>
						<van-button
							hairline
							@click="eyeshieldhandle"
							:class="{ changbgactive: changebg === '#c6efcc' }"
							>护眼</van-button
						>
					</div>
					<van-nav-bar
						@click-left="onClickLeft"
						@click-right="onClickRight"
					>
						<template #left>
							<span
								:class="{
									isprevactive:
										chapter.chapters[lacchapter[0].section]
											.order === 1,
								}"
								>上一章</span
							>
						</template>
						<template #title>
							<span @click="ToBookliste">目录</span>
						</template>
						<template #right>
							<span>下一章</span>
						</template>
					</van-nav-bar>
				</div>
			</transition>
		</div>
		<div v-else class="nobook" @click="clickhandle">
			<van-skeleton title :row="20" />
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex'
export default {
	data() {
		return {
			book: null,
			cpcontents: null,
			showtool: false,
			homeshow: false,
			progress: 25,
			changebg: '#eee6dd',
			changefc: '#000',
			showchapter: false,
			isprev: false,
			lacchapter: [{ section: 0 }],
			contentheight: 0,
			pagecount: 0,
			isfirst: true,
			isshadetip: true,
		}
	},
	computed: {
		...mapState(['chapter']),
		fontSize: function() {
			switch (this.progress) {
				case 0:
					return 14
				case 25:
					return 16
				case 50:
					return 18
				case 75:
					return 20
				default:
					return 22
			}
		},
	},
	created() {
		// localStorage.setExpire('chapter', '9', 24 * 60 * 60 * 1000)

		if (this.chapter) {
			if (localStorage.getarray('chapter')) {
				// console.log('存在', localStorage.getExpire('chapter'))
				this.lacchapter = localStorage
					.getarray('chapter')
					.filter((e) => e.id === this.$route.query.id)
				// console.log(this.$route.query.id,this.lacchapter[0].section);
				// console.log(this.lacchapter)
				// console.log(this.chapter.chapters[this.lacchapter.data])
				this.axios
					.get(
						'http://novel.kele8.cn/chapters/' +
							encodeURIComponent(
								this.chapter.chapters[
									this.lacchapter[0].section
								].link
							)
					)
					.then((res) => {
						// console.log('isnull', res)
						this.book = res.data.chapter
						// console.log(this.book.cpContent)

						this.cpcontents = this.book.cpContent.split(/\n/)
						setTimeout(() => {
							this.isshadetip = false
						}, 1000)

						// console.log(this.cpcontent)

						// console.log(this.book)
						// this.book =
					})
			} else {
				// console.log('不存在', localStorage.getExpire('chapter'))
				this.axios
					.get(
						'http://novel.kele8.cn/chapters/' +
							encodeURIComponent(
								this.chapter.chapters[
									this.lacchapter[0].section
								].link
							)
					)
					.then((res) => {
						// console.log('isnull', res)
						this.book = res.data.chapter
						this.cpcontents = this.book.cpContent.split(/\n/)
						setTimeout(() => {
							this.isshadetip = false
						}, 1000)
						// console.log(this.cpcontent)

						// console.log(this.book)
						// this.book =
					})
			}
		} else {
			this.axios
				.get(
					'http://novel.kele8.cn/book-sources?view=summary&book=' +
						this.$route.query.id
				)
				.then((res) => {
					// 获取书籍源id
					// console.log(res.data[0]._id)

					this.axios
						.get(
							'http://novel.kele8.cn/book-chapters/' +
								res.data[0]._id
						)
						.then((res) => {
							// console.log(res)
							this.$store.commit('updatedChapter', res.data)
							// console.log(localStorage.getExpire('chapter'))

							if (localStorage.getarray('chapter')) {
								this.lacchapter = localStorage.getarray(
									'chapter'
								)
								this.lacchapter = localStorage
									.getarray('chapter')
									.filter(
										(e) => e.id === this.$route.query.id
									)

								this.axios
									.get(
										'http://novel.kele8.cn/chapters/' +
											encodeURIComponent(
												this.chapter.chapters[
													this.lacchapter[0].section
												].link
											)
									)
									.then((res) => {
										// console.log('isnull', res)
										this.book = res.data.chapter
										this.cpcontents = this.book.cpContent.split(
											/\n/
										)
										setTimeout(() => {
											this.isshadetip = false
										}, 1000)
										// console.log(this.cpcontent)

										// console.log(this.book)
										// this.book =
									})
							} else {
								console.log(
									'不存在',
									localStorage.getExpire('chapter')
								)
								this.axios
									.get(
										'http://novel.kele8.cn/chapters/' +
											encodeURIComponent(
												this.chapter.chapters[
													this.lacchapter[0].section
												].link
											)
									)
									.then((res) => {
										// console.log('isnull', res)
										this.book = res.data.chapter
										this.cpcontents = this.book.cpContent.split(
											/\n/
										)
										setTimeout(() => {
											this.isshadetip = false
										}, 1000)
										// console.log(this.cpcontent)

										// console.log(this.book)
										// this.book =
									})
							}
						})
				})
		}
	},
	updated() {
		if (this.isfirst && this.book) {
			this.$refs.bookcontent.style.height = 'auto'
			this.contentheight = Math.round(
				this.$refs.bookcontent.offsetHeight /
					this.$refs.wrapper.offsetHeight
			)
			// console.log(
			// 	this.$refs.bookcontent.offsetHeight,
			// 	this.$refs.wrapper.offsetHeight
			// )
			// console.log(
			// 	this.$refs.bookcontent.offsetHeight /
			// 		this.$refs.wrapper.offsetHeight,
			// 	'111'
			// )
            this.$refs.bookcontent.style.height = '100%'
            
		}
            
        if(this.$refs.chapteritem){
            this.$refs.chapteritem.scrollTop= 47*(this.lacchapter[0].section+1)
        }

		// console.log("update",this.contentheight);

		// let that = this

		// that.$nextTick(function() {
		//      //输出：修改后的值
		//     this.$refs.bookcontent.style.height = "auto"
		//     this.contentheight = Math.ceil(this.$refs.bookcontent.offsetHeight/this.$refs.wrapper.offsetHeight)
		//     console.log(this.contentheight,"next");
		// })
	},

	methods: {
		prevpage() {
			if (this.pagecount === 0) {
				this.onClickLeft()
				return false
			}
			this.isfirst = false
			this.$refs.bookcontent.style.height = '100%'
			let width = document.body.offsetWidth
			this.pagecount = this.pagecount <= 0 ? 0 : this.pagecount - 1
			// console.log(this.pagecount)

			this.$refs.bookcontent.style.transform = `translateX(-${(width -
				16) *
				this.pagecount}px)`
		},
		nextpage() {
			if (this.pagecount === this.contentheight - 1) {
				this.onClickRight()
				return false
			}
			this.isfirst = false

			this.$refs.bookcontent.style.height = '100%'
			let width = document.body.offsetWidth

			// this.pagecount = this.pagecount>this.contentheight?this.contentheight:this.pagecount + 1
			this.pagecount =
				this.pagecount >= this.contentheight - 1
					? this.contentheight - 1
					: this.pagecount + 1
			// console.log("111",this.pagecount,this.contentheight)
			this.$refs.bookcontent.style.transform = `translateX(-${(width -
				16) *
				this.pagecount}px)`
		},
		onClickLeft() {
			// console.log(this.chapter.chapters[this.lacchapter[0].section].order)
			this.isfirst = true
			let width = document.body.offsetWidth
			this.pagecount = 0
			this.$refs.bookcontent.style.transform = `translateX(-${(width -
				16) *
				this.pagecount}px)`
			if (this.chapter.chapters[this.lacchapter[0].section].order > 1) {
				if (
					this.chapter.chapters[this.lacchapter[0].section - 1].isVip
				) {
					return false
				} else {
					this.book = null
					this.showchapter = false
					this.showtool = false
					let time = Date.now()
					this.chapterhostory = localStorage.getarray('chapter') || []
					this.chapterhostory = this.chapterhostory.filter(
						(e) => e.id !== this.$route.query.id
					)
					this.chapterhostory.push({
						id: this.$route.query.id,
						section:
							this.chapter.chapters[
								this.lacchapter[0].section - 1
							].order - 1,
						time,
						expire: 5 * 60 * 60 * 1000,
					})
					localStorage.setItem(
						'chapter',
						JSON.stringify(this.chapterhostory)
					)
					this.lacchapter = localStorage
						.getarray('chapter')
						.filter((e) => e.id === this.$route.query.id)

					// console.log(this.chapter.chapters)
					this.axios
						.get(
							'http://novel.kele8.cn/chapters/' +
								encodeURIComponent(
									this.chapter.chapters[
										this.lacchapter[0].section
									].link
								)
						)
						.then((res) => {
							this.book = res.data.chapter
							// console.log('isnull', this.book.cpContent)

							this.cpcontents = this.book.cpContent.split(/\n/)
						})
				}
			}
		},
		onClickRight() {
			this.isfirst = true
			let width = document.body.offsetWidth
			this.pagecount = 0
			this.$refs.bookcontent.style.transform = `translateX(-${(width -
				16) *
				this.pagecount}px)`
			// console.log(this.chapter.chapters[this.lacchapter[0].section - 1].isVip);
			if (this.chapter.chapters[this.lacchapter[0].section + 1].isVip) {
				// console.log("true");
				return false
			} else {
				// console.log("123");
				this.book = null
				this.showchapter = false
				this.showtool = false
				let time = Date.now()
				this.chapterhostory = localStorage.getarray('chapter') || []
				this.chapterhostory = this.chapterhostory.filter(
					(e) => e.id !== this.$route.query.id
				)
				this.chapterhostory.push({
					id: this.$route.query.id,
					section:
						this.chapter.chapters[this.lacchapter[0].section + 1]
							.order - 1,
					time,
					expire: 5 * 60 * 60 * 1000,
				})
				localStorage.setItem(
					'chapter',
					JSON.stringify(this.chapterhostory)
				)
				this.lacchapter = localStorage
					.getarray('chapter')
					.filter((e) => e.id === this.$route.query.id)

				// console.log(this.chapter.chapters)
				this.axios
					.get(
						'http://novel.kele8.cn/chapters/' +
							encodeURIComponent(
								this.chapter.chapters[
									this.lacchapter[0].section
								].link
							)
					)
					.then((res) => {
						// console.log('isnull', res)
						this.book = res.data.chapter
						// console.log('isnull', this.book.cpContent)

						this.cpcontents = this.book.cpContent.split(/\n/)
					})
			}
		},
		clickhandle() {
			this.isfirst = false

			// console.log(this.showtool)
			this.showtool = !this.showtool
			this.homeshow = false
			this.showchapter = false
		},
		ToBookliste() {
			
			this.showtool = false
            this.showchapter = true
            // console.log()
		},
		abatehandle() {
			// console.log("abatehandle");
			this.progress = this.progress <= 0 ? 0 : this.progress - 25
		},
		addhandle() {
			// console.log("addhandle");
			this.progress = this.progress >= 100 ? 100 : this.progress + 25
		},
		defaulthandle() {
			this.changebg = '#eee6dd'
			this.changefc = '#000'
		},
		nightshandle() {
			this.changebg = '#0c0c0c'
			this.changefc = '#666'
		},
		eyeshieldhandle() {
			this.changebg = '#c6efcc'
			this.changefc = 'rgba(12,46,16,.8)'
		},
		readbook(order, isVIP) {
			this.isfirst = true

			if (isVIP) {
				return false
			} else {
				// console.log(order)
				let width = document.body.offsetWidth
				this.pagecount = 0
				this.$refs.bookcontent.style.transform = `translateX(-${(width -
					16) *
					this.pagecount}px)`
				this.book = null
				this.showchapter = false
				let time = Date.now()
				this.chapterhostory = localStorage.getarray('chapter') || []
				if (this.chapterhostory.length > 0) {
					// console.log(this.chapterhostory, id)
					this.chapterhostory = this.chapterhostory.filter(
						(e) => e.id !== this.$route.query.id
					)
					this.chapterhostory.push({
						id: this.$route.query.id,
						section: order - 1,
						time,
						expire: 5 * 60 * 60 * 1000,
					})
					localStorage.setItem(
						'chapter',
						JSON.stringify(this.chapterhostory)
					)
				} else {
					this.chapterhostory.push({
						id: this.$route.query.id,
						section: order - 1,
						time,
						expire: 20 * 60 * 1000,
					})
					localStorage.setItem(
						'chapter',
						JSON.stringify(this.chapterhostory)
					)
				}
				this.lacchapter = localStorage
					.getarray('chapter')
					.filter((e) => e.id === this.$route.query.id)

				// console.log(this.chapter.chapters)
				this.axios
					.get(
						'http://novel.kele8.cn/chapters/' +
							encodeURIComponent(
								this.chapter.chapters[
									this.lacchapter[0].section
								].link
							)
					)
					.then((res) => {
						// console.log('isnull', res)
						this.book = res.data.chapter
						this.cpcontents = this.book.cpContent.split(/\n/)
					})
			}
		},
	},
}
</script>

<style lang="less">
.readbook {
	min-height: 100vw;
	.headermove-enter-active {
		animation: slideInDown;
		animation-duration: 0.5s;
	}
	.headermove-leave-active {
		animation: slideOutUp;
		animation-duration: 0.5s;
	}
	.leftmove-enter-active {
		animation: slideInLeft;
		animation-duration: 0.2s;
	}
	.leftmove-leave-active {
		animation: slideOutLeft;
		animation-duration: 0.2s;
	}
	.footermove-enter-active {
		animation: slideInUp;
		animation-duration: 0.5s;
	}
	.footermove-leave-active {
		animation: slideOutDown;
		animation-duration: 0.5s;
	}

	.headnav {
		position: fixed;
		top: 0;
		left: 0;
		width: 100vw;
		z-index: 1000;
		.van-nav-bar__content {
			background-color: rgba(50, 51, 52, 0.9);

			.van-icon {
				color: #fff;
				// font-weight: 700;
				font-size: 20px;
			}
			.van-nav-bar__text {
				color: #fff;
				font-weight: 700;
			}
		}
		ul {
			position: absolute;
			right: 10px;
			top: 50px;
			li {
				padding: 5px 20px;
				background: #323334;
				color: #ababab;
				font-size: 14px;

				&:nth-child(2) {
					border-top: 1px solid #000;
					border-bottom: 1px solid #000;
				}
				.van-icon {
					vertical-align: text-top;
					padding-right: 10px;
					color: #ababab;
				}
			}
		}
	}

	.footernav {
		width: 100vw;
		position: fixed;
		left: 0;
		bottom: 0;
		z-index: 1000;
		// background: wheat;
		background-color: rgba(50, 51, 52, 0.9);
		.van-nav-bar {
			background: transparent;
			padding: 20px 0;
			.van-nav-bar__content {
				.van-nav-bar__left,
				.van-nav-bar__right {
					span {
						color: #fff;
						// font-weight: 700;
						font-size: 18px;
					}
					span.isprevactive {
						color: #999;
					}
				}

				.van-nav-bar__title {
					color: #ababab;
				}
			}
		}
		.change-font-size {
			display: flex;
			padding: 20px 0;
			span.changebtn {
				flex: 0 0 15vw;
				text-align: center;
				color: #fff;
			}
			span.changeactive {
				color: #ababab;
			}
			.progressbar {
				flex: 1;
				padding-top: 8px;
			}
		}
		.change-background {
			display: flex;
			justify-content: space-around;
			.van-button {
				flex: 0 0 25vw;
				height: 30px;
				background: transparent;
				color: #fff;
			}
			.changbgactive {
				border-color: #b93221;
				color: #b93221;
			}
		}
	}
	.chapteritem {
		position: fixed;
		left: 0;
		top: 0;
		width: 80vw;
		height: 100vh;
		background: #fff;
		overflow: auto;
		z-index: 100;
		h3 {
			margin: 0;
			padding: 10px 20px;
			border: 1px solid #f8f8f8;
			color: #999;
		}
		ul {
			padding: 0 15px;
			li {
				padding: 15px 0;
				padding-right: 30px;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
				color: #999;
				font-size: 12px;
				border-bottom: 1px solid #f8f8f8;
				position: relative;
				i {
					width: 47px;
					height: 47px;
					position: absolute;
					top: 0;
					right: 0;
					background: url(data:image/png;base64,)
						no-repeat 50%;
					background-size: 1em;
				}
			}
			li.itemactive {
				color: #b93221;
			}
		}
