<template>
	<div v-if="hotrank && highopinion && topsearch && finishrank">
		<SundryRank
			:books="hotrank"
			:title="hotranktitle.shortTitle"
			@clickhandle="clickhandle(hotranktitle.id)"
		/>
		<SundryRank
			:books="highopinion"
			:title="highopiniontitle.shortTitle"
			@clickhandle="clickhandle(highopiniontitle.id)"
		/>
		<SundryRank
			:books="topsearch"
			:title="topsearchtitle.shortTitle"
			@clickhandle="clickhandle(topsearchtitle.id)"
		/>
		<SundryRank
			:books="finishrank"
			:title="finishranktitle.shortTitle"
			@clickhandle="clickhandle(finishranktitle.id)"
		/>
	</div>
	<div v-else>
		<Loading />
	</div>
</template>

<script>
import Loading from '../components/Loading.vue'
import SundryRank from '../components/Rank/SundryRank.vue'
export default {
	data() {
		return {
			hotrank: null,
			hotranktitle: null,
			highopinion: null,
			highopiniontitle: null,
			topsearch: null,
			topsearchtitle: null,
			finishrank: null,
			finishranktitle: null,
		}
	},
	components: {
		SundryRank,
		Loading,
	},
	methods: {
		clickhandle: function(id) {
            this.$router.push({
                path:"/rankbooks",
                query:{
                    id
                }
            })
			// console.log(123, id)
		},
	},
	created() {
		this.axios
			.get('http://novel.kele8.cn/rank/54d43437d47d13ff21cad58b')
			.then((res) => {
				this.hotrank = res.data.ranking.books
				this.hotrank = this.hotrank.slice(0, 3)
				this.hotranktitle = res.data.ranking
				// console.log(res)
			})
		this.axios
			.get('http://novel.kele8.cn/rank/5a684551fc84c2b8efaab179')
			.then((res) => {
				this.highopinion = res.data.ranking.books
				this.highopinion = this.highopinion.slice(0, 3)
				this.highopiniontitle = res.data.ranking
			})
		this.axios
			.get('http://novel.kele8.cn/rank/5a684515fc84c2b8efaa9875')
			.then((res) => {
				this.topsearch = res.data.ranking.books
				this.topsearch = this.topsearch.slice(0, 3)
				this.topsearchtitle = res.data.ranking
			})
		this.axios
			.get('http://novel.kele8.cn/rank/564eb8a9cf77e9b25056162d')
			.then((res) => {
				this.finishrank = res.data.ranking.books
				this.finishrank = this.finishrank.slice(0, 3)
				this.finishranktitle = res.data.ranking
			})
	},
}
</script>

<style></style>
