<template>
	<div id="finishbooks">
		<AllTitle :title="title" @onClickLeft="onClickLeft" />
		<div class="girlfinish">
			<HomeTitel>
				<template v-slot:left>
					<h3>女频完本</h3>
				</template>
			</HomeTitel>
			<ul v-if="girlfinish">
				<li v-for="books in girlfinish" :key="books._id" @click="Tobook(books._id)">
					<DisplayBooks :books="books" />
				</li>
			</ul>
		</div>
		<div class="boyfinish">
			<HomeTitel>
				<template v-slot:left>
					<h3>男频完本</h3>
				</template>
			</HomeTitel>
            <ul v-if="boyfinish">
				<li v-for="books in boyfinish" :key="books._id" @click="Tobook(books._id)">
					<DisplayBooks :books="books" />
				</li>
			</ul>
		</div>
        <HomeFooter />
	</div>
</template>

<script>
import AllTitle from '../components/AllTitle.vue'
import DisplayBooks from '../components/Home/DisplayBooks.vue'
import HomeFooter from '../components/Home/HomeFooter.vue'
import HomeTitel from '../components/Home/HomeTitel.vue'
export default {
	data() {
		return {
			title: '完本',
			girlfinish: null,
			boyfinish: null,
		}
	},
	components: { AllTitle, HomeTitel, DisplayBooks, HomeFooter },
	methods: {
		onClickLeft: function() {
			this.$router.push({
				path: '/',
			})
        },
        Tobook(id){
            this.$router.push({
                path:"/bookdetails",
                query:{
                    id
                }
            })
        }
	},
	created() {
		this.axios
			.get('http://novel.kele8.cn/rank/564eb8a9cf77e9b25056162d')
			.then((res) => {
                this.girlfinish = res.data.ranking.books
                this.girlfinish = this.girlfinish.slice(0, 5)
            })
        this.axios
			.get('http://novel.kele8.cn/rank/564eb878efe5b8e745508fde')
			.then((res) => {
                this.boyfinish = res.data.ranking.books
                this.boyfinish = this.boyfinish.slice(0, 5)
			})
	},
}
</script>

<style lang="less">
#finishbooks{
    .girlfinish,.boyfinish{
        border-bottom: 5px solid #f4f4f4;
        padding-bottom: 10px;
    }
}
</style>
