<template>
	<div id="rankbooks">
		<AllTitle :title="title" @onClickLeft="onClickLeft" />
		<ul v-if="rankbooks" class="rank">
			<li v-for="books in rankbooks" :key="books._id" @click="Tobook(books._id)">
				<DisplayBooks :books="books" />
			</li>
		</ul>
        <div v-else>
            <Loading />
        </div>
        <HomeFooter />
	</div>
</template>

<script>
import Loading from '../components/Loading.vue'
import AllTitle from '../components/AllTitle.vue'
import DisplayBooks from '../components/Home/DisplayBooks.vue'
import HomeFooter from '../components/Home/HomeFooter.vue'
export default {
	data() {
		return {
            title: null,
            rankbooks:null
		}
	},
	components: { AllTitle, DisplayBooks, HomeFooter, Loading },
	created() {
		// console.log(this.$route.query.id)
		this.axios
			.get('http://novel.kele8.cn/rank/' + this.$route.query.id)
			.then((res) => {
				// console.log(res)
                this.title = res.data.ranking.shortTitle
                this.rankbooks=res.data.ranking.books
			})
	},

	methods: {
		onClickLeft: function() {
			this.$router.push({
				path: '/ranking',
			})
        },
        Tobook(id){
             this.$router.push({
                path:"/bookdetails",
                query:{
                    id
                }
            })
        }
	},
}
</script>

<style lang="less">
#rankbooks{
    .rank{
        border-top: 5px solid #f4f4f4;
        border-bottom: 5px solid #f4f4f4;
    }
}
</style>
