<template>
	<div id="home">
		<HomeHeader />
		<HomeNav />
		<div class="homehotbooks">
			<HaveCall @havecallclick="havecallclick" />
			<ul v-if="hotbook">
				<li
					v-for="books in hotbook"
					:key="books._id"
					@click="Tobook(books._id)"
				>
					<DisplayBooks :books="books" />
				</li>
			</ul>
			<Loading v-else />
		</div>
		<div class="allwatch">
			<AllWatch :allwatchid="allwatchid"/>
			<ShowBooks :allbooks="allwatch" @Tobook="Tobook" v-if="allwatch" />
			<Loading v-else />
		</div>
		<div class="goddessrecommend">
			<GoddessRec :goddessrecid="goddessrecid"/>
			<div v-if="goddessrec && goddessrecnext">
				<ShowBooks :allbooks="goddessrec" @Tobook="Tobook" />
				<ShowBookst :recnext="goddessrecnext" @Tobook="Tobook" />
			</div>

			<Loading v-else />
		</div>
		<div class="boysrecommend">
			<BoysRec :boysrecid="boysrecid"/>
			<div v-if="boysrec && boysnext">
				<ShowBooks :allbooks="boysrec" @Tobook="Tobook" />
				<ShowBookst :recnext="boysnext" @Tobook="Tobook" />
			</div>
			<Loading v-else />
		</div>
		<HomeFooter />
	</div>
</template>

<script>
// @ is an alias to /src
import HomeFooter from '../components/Home/HomeFooter.vue'
import HomeHeader from '../components/Home/HomeHeader.vue'
import HomeNav from '../components/Home/HomeNav.vue'
import HaveCall from '../components/Home/HaveCall.vue'
import DisplayBooks from '../components/Home/DisplayBooks.vue'
import GoddessRec from '../components/Home/GoddessRec.vue'
import BoysRec from '../components/Home/BoysRec.vue'
import AllWatch from '../components/Home/AllWatch.vue'
import ShowBooks from '../components/Home/ShowBooks.vue'
import ShowBookst from '../components/Home/ShowBookst.vue'
import Loading from '../components/Loading.vue'

export default {
	name: 'Home',
	data() {
		return {
			hotbooks: null,
            hotbook: null,
			goddessrec: null,
			goddessrecnext: null,
			boysrec: null,
			boysnext: null,
			allwatch: null,
            hi: 0,
            goddessrecid:null,
            allwatchid:null,
            boysrecid:null
		}
	},
	components: {
		// HelloWorld
		HomeHeader,
		HomeFooter,
		HomeNav,
		HaveCall,
		DisplayBooks,
		GoddessRec,
		AllWatch,
		ShowBooks,
		ShowBookst,
		BoysRec,
		Loading,
	},
	methods: {
		havecallclick: function() {
			this.hi = this.hi < this.hotbooks.length / 4 - 1 ? this.hi + 1 : 0

			this.hotbook = this.hotbooks.slice(4 * this.hi, (this.hi + 1) * 4)
			// console.log(this.hotbook,this.hi)
		},
		Tobook: function(id) {
			// console.log(id);
			this.$router.push({
				path: '/bookdetails',
				query: {
					id,
				},
			})
		},
	},
	created() {
		this.axios
			.get('http://novel.kele8.cn/rank/54d42d92321052167dfb75e3')
			.then((res) => {
				this.hotbooks = res.data.ranking.books
				this.hotbook = this.hotbooks.slice(0, 4)
				// console.log(this.hotbooks)
			})
		this.axios
			.get('http://novel.kele8.cn/rank/54d43437d47d13ff21cad58b')
			.then((res) => {
                this.goddessrecid = res.data.ranking.id
				this.goddessrec = res.data.ranking.books
				this.goddessrecnext = this.goddessrec.slice(4, 8)
				this.goddessrec = this.goddessrec.slice(0, 4)
			})
		this.axios
			.get('http://novel.kele8.cn/rank/5a39d435fc84c2b8ef884798')
			.then((res) => {
                this.allwatchid = res.data.ranking.id
				this.allwatch = res.data.ranking.books
				this.allwatch = this.allwatch.slice(0, 4)
				// console.log("123",this.allwatch )
			})
		this.axios
			.get('http://novel.kele8.cn/rank/5a6844f8fc84c2b8efaa8bc5')
			.then((res) => {
                this.boysrecid = res.data.ranking.id
				this.boysrec = res.data.ranking.books
				this.boysnext = this.boysrec.slice(4, 8)
				this.boysrec = this.boysrec.slice(0, 4)
			})
	},
}
</script>
<style lang="less">
#home {
	.homehotbooks {
		border-bottom: 5px solid #f4f4f4;
	}
	.allwatch {
		border-bottom: 5px solid #f4f4f4;
	}
	.goddessrecommend {
		border-bottom: 5px solid #f4f4f4;
	}
	.boysrecommend {
		border-bottom: 5px solid #f4f4f4;
	}
}
</style>
