<template>
	<div id="bookdetail">
		<AllTitle :title="title" @onClickLeft="onClickLeft" />
		<div class="content" v-if="book">
			<van-card
				:title="book.title"
				:thumb="`http://statics.zhuishushenqi.com${book.cover}`"
			>
				<template #desc>
					<h2>
						作者 : <span>{{ book.author }}</span>
					</h2>
					<h3>
						字数 ： {{ parseInt(book.wordCount / 10000) }}万字 |
						<span v-if="book.isSerial">连载中</span>
						<span v-else>已完结</span>
					</h3>
				</template>
				<template #tags>
					<van-tag plain type="danger">{{ book.majorCate }}</van-tag>
				</template>
			</van-card>
			<div class="btn">
				<div class="read" @click="toreadbook" v-if="isread">
					开始阅读
				</div>
				<div class="read" @click="toreadbook" v-else>继续阅读</div>
				<div class="add" @click="addbookstack" v-if="isbookstack">
					加入书架
				</div>
				<div class="add" @click="removebook" v-else>移出书架</div>
			</div>
			<div class="summary">
				<p :class="{ pactive: isiclick }">
					简介 ： {{ book.longIntro }}
				</p>
				<i
					:class="{ iactive: isiclick }"
					@click="isiclick = !isiclick"
				></i>
			</div>
			<div @click="chapterhandle">
				<van-nav-bar class="lastchapter" right-text="目录">
					<template #left>
						<h2>
							最新：<span>{{ book.lastChapter }}</span>
						</h2>
					</template>
				</van-nav-bar>
			</div>
			<div class="comment">
				<HomeTitel @clickhandle="comment">
					<template v-slot:left>
						<h3>评论</h3>
					</template>
					<template v-slot:right>
						<span>我要评论</span>
					</template>
				</HomeTitel>
				<Reviews
					:reviews="reviews"
					v-if="reviews && reviews.length > 0"
				/>
				<div v-else class="notreviews">
					<span>暂无评论</span>
				</div>
			</div>
			<div class="recommend">
				<HomeTitel @clickhandle="recommend">
					<template v-slot:left>
						<h3>你可能感兴趣的</h3>
					</template>
					<template v-slot:right>
						<span>换一换</span>
					</template>
				</HomeTitel>

				<ShowBooks
					:allbooks="recommendbooks"
					class="recommendbooks"
					@Tobook="Tobook"
				/>
			</div>
		</div>
		<div v-else>
			<Loading />
		</div>
		<HomeFooter />
	</div>
</template>

<script>
import AllTitle from '../components/AllTitle.vue'
import HomeFooter from '../components/Home/HomeFooter.vue'
import HomeTitel from '../components/Home/HomeTitel.vue'
import ShowBooks from '../components/Home/ShowBooks.vue'
import Loading from '../components/Loading.vue'
import Reviews from '../components/Reviews.vue'
import { mapState } from 'vuex'

export default {
	components: {
		AllTitle,
		HomeTitel,
		ShowBooks,
		HomeFooter,
		Loading,
		Reviews,
	},
	data() {
		return {
			title: '书籍详情',
			chapterhostory: null,
			stackhostroy: null,
			book: null,
			isiclick: true,
			isread: true,
            hasbook: null,
            allrecommend:null,
            count:0,
			recommendbooks: null,
			fromid: null,
			reviews: null,
			isbookstack: true,
			inbookstack: null,
		}
	},
	methods: {
		removebook() {
            this.isbookstack = true
			this.inbookstack = localStorage.getarray('bookstack') || []
			this.inbookstack = this.inbookstack.filter(
				(e) => e.id !== this.$route.query.id
            )
            localStorage.setItem(
						'bookstack',
						JSON.stringify(this.inbookstack)
					)
            console.log( localStorage.getarray('bookstack'))
		},
		addbookstack() {
            this.isbookstack = false
			let time = Date.now()
			this.stackhostroy = localStorage.getarray('bookstack') || []
			// console.log("122",this.chapterhostory);
			if (this.stackhostroy.length > 0) {
				let arr = []
				arr = this.stackhostroy.filter((e) => e.id === this.book._id)
				// console.log("arr:",arr,this.stackhostroy)

				if (arr.length === 0) {
					// console.log(this.stackhostroy)
					this.stackhostroy.push({
						id: this.book._id,
						title: this.book.title,
						cover: this.book.cover,
						time,
						expire: 5 * 60 * 60 * 1000,
					})
					localStorage.setItem(
						'bookstack',
						JSON.stringify(this.stackhostroy)
					)
				}
			} else {
				this.stackhostroy.push({
					id: this.book._id,
					title: this.book.title,
					cover: this.book.cover,
					time,
					expire: 5 * 60 * 60 * 1000,
				})
				localStorage.setItem(
					'bookstack',
					JSON.stringify(this.stackhostroy)
				)
			}
		},
		toreadbook() {
			// console.log("kais");
			let time = Date.now()
			this.chapterhostory = localStorage.getarray('chapter') || []
			// console.log("122",this.chapterhostory);
			if (this.chapterhostory.length > 0) {
				let arr = []
				arr = this.chapterhostory.filter(
					(e) => e.id === this.$route.query.id
				)
				console.log('arr:', arr, this.chapterhostory)

				if (arr.length === 0) {
					console.log(this.chapterhostory)
					this.chapterhostory.push({
						id: this.$route.query.id,
						section: 0,
						time,
						expire: 5 * 60 * 60 * 1000,
					})
					localStorage.setItem(
						'chapter',
						JSON.stringify(this.chapterhostory)
					)
				}
			} else {
				this.chapterhostory.push({
					id: this.$route.query.id,
					section: 0,
					time,
					expire: 20 * 60 * 1000,
				})
				localStorage.setItem(
					'chapter',
					JSON.stringify(this.chapterhostory)
				)
			}
			this.$router.push({
				path: './readbook',
				query: { id: this.$route.query.id },
			})
		},
		onClickLeft: function() {
			console.log(this.prevpath)
			if (this.prevpath === '/classifybooks') {
				this.$router.push({
					path: this.prevpath,
					query: {
						gender: this.$route.query.gender,
						name: this.$route.query.name,
					},
				})
			} else {
				if (this.prevpath === '/rankbooks') {
					this.$router.push({
						path: this.prevpath,
						query: {
							id: this.fromid,
						},
					})
				} else {
					if (this.prevpath === '/searchresult') {
						this.$router.push({
							path: this.prevpath,
							query: {
								value: this.book.title,
							},
						})
					} else {
						// console.log(this.$router)
						this.$router.push({
							path: this.prevpath,
							// path: '/',
						})
					}
				}
			}
		},
		chapterhandle: function() {
			this.$router.push({
				path: '/booklists',
				query: {
					id: this.$route.query.id,
				},
			})
		},
		comment() {
			console.log('请先登录')
		},
		recommend() {
            // console.log("more");
            this.count = this.count < this.allrecommend.length / 4 - 1 ? this.count + 1 : 0

			this.recommendbooks = this.allrecommend.slice(4 * this.count, (this.count + 1) * 4)
        },
		Tobook(id) {
			console.log(id)
			this.book = null
			//改变当前路由query.id
			let query = this.$router.history.current.query
			let path = this.$router.history.current.path
			//对象的拷贝
			let newQuery = JSON.parse(JSON.stringify(query))
			newQuery.id = id
			this.$router.push({ path, query: newQuery })
			this.axios
				.get('http://novel.kele8.cn/book-info/' + id)
				.then((res) => {
					this.book = res.data
					// console.log(this.book)
				})
			this.axios
				.get('http://novel.kele8.cn/recommend/' + id)
				.then((res) => {
					this.recommendbooks = res.data.books
					this.recommendbooks = this.recommendbooks.slice(1, 5)
					// console.log(this.recommendbooks)
				})
			this.axios
				.get(
					'http://novel.kele8.cn/book/reviews?book=' +
						id +
						'&start=0&limit=5'
				)
				.then((res) => {
					this.reviews = res.data.reviews
				})
		},
	},
	computed: {
		...mapState(['prevpath']),
	},
	beforeRouteEnter(to, from, next) {
		// console.log(from.path)
		if (from.path !== '/readbook' && from.path !== '/booklists') {
			next((vm) => {
				vm.$store.commit('updatedPrevpath', from.path)
				vm.fromid = from.query.id
				// console.log(vm, 'from')
				// console.log(vm.prevpath, 'prevpath')
			})
		} else {
			next()
		}
	},
	beforeRouteLeave(to, from, next) {
		// console.log(to, from)
		if (to.name == 'ReadBook' || to.name == 'BookLists') {
			this.axios
				.get(
					'http://novel.kele8.cn/book-sources?view=summary&book=' +
						this.$route.query.id
				)
				.then((res) => {
					// 获取书籍源id
					// console.log(res.data[0]._id)

					this.axios
						.get(
							'http://novel.kele8.cn/book-chapters/' +
								res.data[0]._id
						)
						.then((res) => {
							// console.log(res)
							this.$store.commit('updatedChapter', res.data)
							next()
						})
				})
		} else {
			next()
		}
	},
	created() {
		// console.log("create",this.prevpath)
		this.hasbook = localStorage.getarray('chapter') || []
		this.hasbook = this.hasbook.filter((e) => e.id === this.$route.query.id)
		if (this.hasbook.length > 0) {
			this.isread = false
		}
		this.inbookstack = localStorage.getarray('bookstack') || []
		this.inbookstack = this.inbookstack.filter(
			(e) => e.id === this.$route.query.id
		)
		if (this.inbookstack.length > 0) {
			this.isbookstack = false
		}
		// console.log(this.inbookstack.length)
		this.axios
			.get('http://novel.kele8.cn/book-info/' + this.$route.query.id)
			.then((res) => {
				this.book = res.data
				// console.log(this.book)
			})
		this.axios
			.get('http://novel.kele8.cn/recommend/' + this.$route.query.id)
			.then((res) => {
				this.allrecommend = res.data.books
				this.recommendbooks = this.allrecommend.slice(0, 4)
				console.log(res.data)
			})
		this.axios
			.get(
				'http://novel.kele8.cn/book/reviews?book=' +
					this.$route.query.id +
					'&start=0&limit=5'
			)
			.then((res) => {
				this.reviews = res.data.reviews
				// console.log(this.reviews)
			})
	},
}
</script>

<style lang="less">
#bookdetail {
	.content {
		border-bottom: 5px solid #eee;
		border-top: 5px solid #eee;
		.van-card {
			background: #fff;
			.van-card__header {
				.van-card__thumb {
					height: 133px;
					width: 100px;
					margin-right: 20px;
					.van-image {
						box-shadow: 2px 3px 2px #6e6e6f;
						img {
							border-radius: 0;
						}
					}
				}
				.van-card__content {
					.van-card__title {
						height: 40px;
						line-height: 40px;
						font-weight: 600;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
						cursor: pointer;
						font-size: 16px;
					}

					h2 {
						font-size: 14px;
						margin: 10px 0 0;
						color: #777;
						font-weight: 500;
					}
					h3 {
						color: #777;
						font-weight: 500;
					}
					.van-tag {
						border: 1px solid #1cb09a;
						color: #1cb09a;
						font-size: 12px;
						padding: 2px 3px;
						-moz-border-radius: 5px;
						-webkit-border-radius: 5px;
						border-radius: 5px;
						&::before {
							border: none;
						}
					}
				}
			}
		}
		.btn {
			display: flex;
			padding: 20px;
			justify-content: space-between;
			div {
				width: 45%;
				text-align: center;
				border: 1px solid;
				padding: 5px 0;
				border-radius: 20px;
			}
			.read {
				border-color: #d56264;
				color: #d56264;
			}
			.add {
				border-color: #1cb09a;
				color: #1cb09a;
			}
		}
		.summary {
			padding: 0 20px;
			margin-bottom: 10px;
			background: #fff;

			p {
				overflow: hidden;
				text-align: left;
				font-size: 14px;
				color: #777;
				margin: 5px 0;
			}
			.pactive {
				height: 60px;
			}
			i {
				width: 100%;
				height: 15px;
				display: inline-block;
				// margin-top: 10px;
				background: #fff
					url(//static1.mtime.cn/html5/20201022145405/images/2014/i-tmore.png)
					no-repeat center center;
				background-size: auto 0.5em;
				transform: rotate(180deg);
				-ms-transform: rotate(180deg);
				-moz-transform: rotate(180deg);
				-webkit-transform: rotate(180deg);
				-o-transform: rotate(180deg);
			}
			.iactive {
				background-size: auto 0.5em;
				transform: rotate(360deg);
				-ms-transform: rotate(360deg);
				-moz-transform: rotate(360deg);
				-webkit-transform: rotate(360deg);
				-o-transform: rotate(360deg);
			}
		}
		.lastchapter {
			border-bottom: 5px solid #eee;
			border-top: 5px solid #eee;
			.van-nav-bar__content {
				.van-nav-bar__left {
					h2 {
						font-size: 12px;
						max-width: 250px;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
						span {
							font-size: 14px;
							color: #777;
						}
					}
				}
				.van-nav-bar__right {
					span {
						color: #1cb09a;
					}
				}
			}
		}
		.recommend {
			border-top: 5px solid #eee;
			.recommendbooks {
				padding: 0 20px;
				.van-grid-item {
					.van-grid-item__content {
						padding: 16px 0;
						.van-image {
							width: 70px;
							height: 90px;
						}
					}
				}
			}
		}
		.comment {
			.notreviews {
				width: 100%;
				text-align: center;
				padding: 20px 0;
				font-size: 14px;
			}
		}
	}
}
</style>
