<template>
	<div id="boollists" v-if="chapters">
		<AllTitle :title="chapter.name" @onClickLeft="onClickLeft" />

		<van-nav-bar
			:right-text="issequence ? '倒序' : '正序'"
			@click-right="onClickRight"
			class="listtitle"
		>
			<template #left>
				<h4>
					目录 <span>共{{ chapter.chapters.length }}章</span>
				</h4>
			</template>
		</van-nav-bar>
		<ul class="chapteritem">
			<li
				v-for="chapteritem in chapters"
				:key="chapteritem.id"
				@click="
					readbook(
						chapteritem.order,
						chapteritem.isVip
					)
				"
			>
				{{ chapteritem.order }} {{ chapteritem.title }}
				<i v-if="chapteritem.isVip"></i>
			</li>
		</ul>
		<van-pagination
			v-model="currentPage"
			:page-count="pagecount"
			force-ellipses
			@change="lefthandle"
			class="pagination"
		>
		</van-pagination>
		<HomeFooter />
	</div>
	<div v-else>
		<Loading />
	</div>
</template>

<script>
import { mapState } from 'vuex'
import AllTitle from '../components/AllTitle.vue'
import HomeFooter from '../components/Home/HomeFooter.vue'
import Loading from '../components/Loading.vue'
export default {
	components: { AllTitle, Loading, HomeFooter },
	data() {
		return {
			currentPage: 1,
			pagecount: 0,
			chapters: null,
			issequence: true,
			allchapter: null,
			readbookhistory: null,
			chapterhostory: null,
		}
	},
	computed: {
		...mapState(['chapter']),
	},
	created() {
		if (this.chapter) {
			this.pagecount = Math.ceil(this.chapter.chapters.length / 20)
			this.allchapter = [...this.chapter.chapters]
			this.chapters = this.allchapter.slice(0, 20)
		} else {
			this.axios
				.get(
					'http://novel.kele8.cn/book-sources?view=summary&book=' +
						this.$route.query.id
				)
				.then((res) => {
					// 获取书籍源id
					console.log(res.data[0]._id)

					this.axios
						.get(
							'http://novel.kele8.cn/book-chapters/' +
								res.data[0]._id
						)
						.then((res) => {
							// console.log(res)
							this.$store.commit('updatedChapter', res.data)
							// console.log(localStorage.getExpire('chapter'))
							this.pagecount = Math.ceil(
								this.chapter.chapters.length / 20
							)
							this.allchapter = [...this.chapter.chapters]
							this.chapters = this.allchapter.slice(0, 20)
						})
				})
		}
	},
	methods: {
		onClickLeft() {
			this.$router.push({
				path: '/bookdetails',
				query: {
					id: this.$route.query.id,
				},
			})
		},
		onClickRight() {
			this.issequence = !this.issequence
			this.allchapter.reverse()
			// console.log(this.allchapter, this.chapter.chapters)
			this.currentPage = 1
			this.chapters = this.allchapter.slice(
				(this.currentPage - 1) * 20,
				this.currentPage * 20
			)
		},
		lefthandle() {
			this.chapters = this.allchapter.slice(
				(this.currentPage - 1) * 20,
				this.currentPage * 20
			)
			// console.log(this.chapters)
		},
		readbook( order, isVIP) {
			if (isVIP) {
				return false
			} else {
				// console.log(order)
				// localStorage.setExpire(
				//     id,
				// 	'chapter',
				// 	order - 1,
				// 	24 * 60 * 60 * 1000
				// )
				let time = Date.now()
				this.chapterhostory = localStorage.getarray('chapter') || []
				if (this.chapterhostory.length > 0) {
					// console.log(this.chapterhostory, id)
					this.chapterhostory = this.chapterhostory.filter((e) => e.id !== this.$route.query.id)
					this.chapterhostory.push({
						id:this.$route.query.id,
						section: order - 1,
						time,
						expire: 5 * 60 * 60 * 1000,
					})
					localStorage.setItem(
						'chapter',
						JSON.stringify(this.chapterhostory)
					)
				} else {
					this.chapterhostory.push({
						id:this.$route.query.id,
						section: order - 1,
						time,
						expire: 20 * 60 * 1000,
					})
					localStorage.setItem(
						'chapter',
						JSON.stringify(this.chapterhostory)
					)
				}
				this.$router.push({
					path: './readbook',
					query: { id: this.$route.query.id },
				})
			}
		},
	},
}
</script>

<style lang="less">
#boollists {
	.alltitle {
		.van-nav-bar__content {
			.van-nav-bar__title {
				font-size: 16px;
			}
		}
	}
	.listtitle {
		.van-nav-bar__content {
			.van-nav-bar__left {
				span {
					color: #777;
					font-weight: normal;
					font-size: 12px;
				}
			}
			.van-nav-bar__right {
				span {
					color: #000;
				}
			}
		}
	}
	.chapteritem {
		padding: 0 20px;
		li {
			padding: 15px 0;
			padding-right: 30px;
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
			color: #999;
			font-size: 12px;
			border-bottom: 1px solid #f8f8f8;
			position: relative;
			i {
				width: 47px;
				height: 47px;
				position: absolute;
				top: 0;
				right: 0;
				background: url(data:image/png;base64,)
					no-repeat 50%;
				background-size: 1em;
			}
		}
	}
	.pagination {
		.van-pagination__item {
			color: #b8232f;
		}
		.van-pagination__item--active {
			color: #fff;
			background-color: #b8232f;
		}
		.van-pagination__item:active {
			color: #fff;
			background-color: #b8232f;
		}
	}
}
</style>
