<template>
	<div id="bookstack">
		<AllTitle :title="'我的书架'" @onClickLeft="onClickLeft" />
		<div v-if="bookstack" class="stack">
			<ul>
				<li v-for="book in bookstack" :key="book.id">
					<van-card
						:title="book.title"
						:thumb="`http://statics.zhuishushenqi.com${book.cover}`"
					>
						<template #desc>
							<van-button
								type="default"
								@click="toreadbook(book.id)"
								>继续阅读</van-button
							>
							<van-button
								type="warning"
								@click="removebook(book.id)"
								>移出书架</van-button
							>
						</template>
					</van-card>
				</li>
			</ul>
		</div>
		<div v-else class="notstack">
			<h2>暂无小说，到书城逛逛吧</h2>
		</div>
		<HomeFooter />
	</div>
</template>

<script>
import AllTitle from '../components/AllTitle.vue'
import HomeFooter from '../components/Home/HomeFooter.vue'
export default {
	components: { AllTitle, HomeFooter },
	data() {
		return {
			bookstack: null,
		}
	},
	methods: {
		toreadbook(id) {
			console.log(id)
			this.$router.push({
				path: '/bookdetails',
				query: {
					id,
				},
			})
		},
		removebook(id) {
			console.log(id)
			this.bookstack = this.bookstack.filter((e) => e.id !== id)
			localStorage.setItem('bookstack', JSON.stringify(this.bookstack))
		},
		onClickLeft() {
			this.$router.push({
				path: '/',
			})
		},
	},
	created() {
		this.bookstack = localStorage.getarray('bookstack')
		// console.log(this.bookstack)
	},
}
</script>
<style lang="less">
#bookstack {
	.stack {
		ul {
			
			padding: 20px 10px;
			li {
				padding: 10px 0;
				border-bottom: 1px solid #eee;
				.van-card {
					background: #fff;
					.van-card__header {
						.van-card__thumb {
							width: 70px;
							box-shadow: 2px 3px 2px #6e6e6f;
							img {
								border-radius: 0;
							}
						}
						.van-card__content {
							margin-left: 15px;
							.van-card__title {
								font-size: 16px;
								white-space: nowrap;
								text-overflow: ellipsis;
								overflow: hidden;
								padding: 10px 0;
							}
							.van-button {
								height: 30px;
								margin-top: 10px;
								&:nth-child(2) {
									margin-right: 20px;
								}
							}
						}
					}
				}
			}
		}
	}
	.notstack {
		min-height: 65vh;
		h2 {
			text-align: center;
		}
	}
}
</style>
