<template>
	<van-nav-bar :title="title" class="alltitle"  @click-left="$emit('onClickLeft')" @click-right="onClickRight">
		<template #left>
			<van-icon name="arrow-left" size="24" color="#fff" />
		</template>
		<template #right>
			<van-icon name="wap-home-o" size="24" color="#fff" />
		</template>
	</van-nav-bar>
</template>

<script>
export default {
    props: ['title'],
    methods: {
        onClickRight:function(){
            this.$router.push({
                path:'/'
            })
        }
    },
}
</script>

<style lang="less">
.alltitle {
	.van-nav-bar__content {
		background: #b8232f;
		.van-nav-bar__title {
			color: #fff;
			font-size: 20px;
		}
	}
}
</style>
